const getters = {
  getUid () {
    return state.uid
  },
  getToken () {
    return state.token
  },
  getUsername () {
    return state.username
  },
  getNickname () {
    return state.nickname
  },
  getLevel () {
    return state.level
  },
  getCurrentPlanetId () {
    const index = state.planets.findIndex(planet => planet.state === true)
    return state.planets[index].pid
  }
}

const mutations = {
  'SET_RESOURCES' (state, data) {
    for (let spendResource of data) {
      state.resources.map(resource => {
        if (resource.type === spendResource.type) {
          resource.amount -= spendResource.amount
        }
      })
    }
  },
  'SET_DIAMOND' (state, data) {
    state.diamond = data
  },
  'SET_USER' (state, data) {
    state.token = data.token
    state.username = data.username
    state.nickname = data.nickname
    state.coin = data.coin
    state.diamond = data.diamond
    state.resourceMax = data.resourceMax
    state.resources = data.resources
    state.baggage = data.baggage
    state.achievements = data.achievements
    state.planets = data.planets
  }
}
const actions = {
  login: async function (context, params) {
    try {
      const result = await axios('/user/login', {
        method: 'post',
        data: JSON.stringify(params)
      })
      context.commit('SET_USER', result.data)
      return true
    } catch (error) {
      console.log(error)
      return false
    }
  },
  register: async function (context, params) {
    try {
      const result = await axios('/user/register', {
        method: 'post',
        data: JSON.stringify(params)
      })
      console.log(result)
      return true
    } catch (error) {
      console.log(error)
      return false
    }
  },

  async spendDiamond (context, params) {
    try {
      if (context.state.diamond >= params.diamond) {
        // const result = await axios('/user/diamond', {
        //   method: 'patch',
        //   data: JSON.stringify(params)
        // })
        const diamond = context.state.diamond - params.diamond
        context.commit('SET_DIAMOND', diamond)
        return true
      }
      console.log('here', context)
    } catch (error) {
      console.log(error)
      return false
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
