<template lang="pug">
  div(class="build-mode")
    p 請點擊地盤興建建築
    div(v-if="isErr" class="err-msg")
      p 此地盤已有建築或位於地圖邊緣，無法興建
    div(class="button-group")
      div
        button(@click="close" class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/leave-cross.png')})`}")
        span 離開
      div
        button(@click="build" class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/building-confirm.png')})`}")
        span 建造
</template>

<script>
export default {
  props: {
    building: Object
  },
  data () {
    return {
      isErr: false,
      module: null
    }
  },
  methods: {
    async build () {
      const result = await this.module.gameFunction.checkIfCanBuild(this.module.Obj.lastCoordinateX, this.module.Obj.lastCoordinateY, this.building.id)
      try {
        if (result) {
          this.$store.commit('user/SET_RESOURCES', this.building.resources)
          this.close()
        } else {
          this.isErr = !result
          setTimeout(() => {
            this.isErr = !this.isErr
          }, 1000)
        }
      } catch (error) {
        console.log(error)
      }
    },
    close () {
      this.$emit('close')
      this.module.gameFunction.setIsOnBuildMode(false)
      this.module.gameFunction.scene.tileGroup.getChildren().map(ele => { ele.clearTint() })
      // this.module.scene.tileGroup.getChildren().map(ele => { ele.clearTint() })
    }
  },
  created () {
    import('@/game/scences/PlayScene').then(async (module) => {
      this.module = module
      this.module.gameFunction.setIsOnBuildMode(true)
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.build-mode {
  display: flex;
  justify-content: center;
  > p:nth-child(1) {
    position: absolute;
    top: 40px;
    text-shadow: map-get($shadow, secondary-4);
  }
  > .err-msg {
    position: absolute;
    top: 266px;
    display: flex;
    width: 316px;
    height: 54px;
    align-items: center;
    justify-content: center;
    background-color: map-get($colors, black);
    opacity: 0.7;
    border: 1px solid map-get($colors, white);
    border-radius: 7.5px;
    box-shadow: map-get($shadow, secondary-4);
    > p {
      font-size: 14px;
      font-weight: 900;
    }
  }
  > .button-group {
    position: absolute;
    top: 576px;
    display: flex;
    justify-content: center;
    > div {
      margin: 0 10px;
      > button {
        display: block;
        width: 37px;
        height: 37px;
        color: map-get($colors, white);
      }
      > span {
        font-size: 10px;
        text-shadow: map-get($shadow, primary-4);
      }
    }
  }
}
</style>
