<template lang="pug">
  div(class="warehouse-root")
    div
      div(class="heading-default")
        span 倉庫庫存
    div
      div(class="heading-default")
        h4 寶石金幣
      div
        div
          i(:style="{'background-image': `url(${require('@/assets/icon/diamond.png')})`}")
          span {{user.diamond}}
        div
          i(:style="{'background-image': `url(${require('@/assets/icon/coin.png')})`}")
          span {{user.coin}}
    div
      div(class="heading-default")
        h4 建築材料
        span 每一資源最大容量 {{user.resourceMax}}
      div
        div(v-for="resource in user.resources" @click="showDetail(resource)")
          i(:style="{'background-image':`url(${transformImage(resource.type)})`}")
          span {{resource.amount}}
    div
      div(class="heading-default")
        h4 背包物品
      div
        div(
          v-for="(item, index) in user.baggage"
          :key="`baggage-${index}`"
          :style="{'background-image': `url(${transformImage(item)})`}")
    modal-card-content(v-if="isShowDetail" @close="isShowDetail = !isShowDetail")
      resource-detail(slot="card" :resource="selectedResource")
</template>
<script>
import { transformImage } from '@/assets/javascript/transformImage.js'
import ModalCardContent from '@/components/ModalCardContent.vue'
import ResourceDetail from '@/components/game/ResourceDetail.vue'

export default {
  components: {
    ModalCardContent,
    ResourceDetail
  },
  props: {
    user: Object
  },
  data () {
    return {
      transformImage: transformImage,
      isShowDetail: false,
      selectedResource: '',
      baggage: []
    }
  },
  methods: {
    showDetail (resource) {
      this.isShowDetail = true
      this.selectedResource = resource
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.warehouse-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 96px 176px auto;
  grid-template-areas:
    "heading"
    "diamond"
    "resource"
    "baggage";
  > div:nth-child(1) {
    box-sizing: border-box;
    padding-bottom: 21px;
    > div {
      text-align: center;
      padding-bottom: 15px;
      text-shadow: map-get($shadow, secondary-4);
    }
  }
  > div:nth-child(2) {
    > div:nth-child(2) {
      display: flex;
      padding-top: 10px;
      justify-content: space-evenly;
      > div {
        display: flex;
        align-items: center;
        > i {
          width: 39px;
          height: 41px;
        }
      }
    }
  }
  > div:nth-child(3) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 34px 34px 34px 34px;
      grid-template-rows: 49px 49px;
      grid-gap: 16px 36.5px;
      justify-content: center;
      padding-top: 12px;
      > div {
        i {
          width: 34px;
          height: 36px;
          background-size: contain;
        }
        span {
          display: block;
          font-size: 12px;
          line-height: 12px;
        }
      }
    }
  }
  > div:nth-child(4) {
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 85px 85px 85px;
      grid-auto-rows: 85;
      grid-gap: 11px 8px;
      padding-top: 17px;
      > div {
        width: 85px;
        height: 85px;
        background-color: #ffffff;
        background-size: cover;
      }
    }
  }
}
</style>
