<template lang="pug">
  div(class="resource-detail-root")
    div(class="heading-default")
      h3 {{resource.type}}
      span 擁有數：{{resource.amount}}
    div
      p 一隻貓被鎖在一個箱子中 ， 箱中的貓同時既是活 ， 也是死 。 當箱子打開時 ， 此量子疊加狀態瓦解 ； 在那瞬間這隻貓是被毒死 ， 或得以保命。
      div
        img(:src="transformImage(resource.type)")
    div
      div(class="heading-default")
        h4 取得路徑
      div
        p(v-for="(way,index) in acquisition") {{index+1}}.{{way}}
    div(class="button-group")
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/gift.png')})`}")
        span 商城
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/build.png')})`}")
        span 交易
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/collect.png')})`}")
        span 採集
</template>

<script>
import { transformImage } from '@/assets/javascript/transformImage.js'

export default {
  props: {
    resource: Object
  },
  data () {
    return {
      transformImage: transformImage,
      acquisition: [
        '完成主線任務 4-3',
        '抽獎活動 ， 低機率獲得',
        '進行採集時 ， 低機率獲得'
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.resource-detail-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 37px 255px 110px 20px 60px;
  grid-template-areas:
    "heading"
    "resource"
    "acquisition"
    "."
    "button";
  > div:nth-child(1) {
    grid-area: heading;
    display: flex;
    justify-content: center;
    position: relative;
    padding-bottom: 13px;
    > h3 {
      font-size: 24px;
      line-height: 24px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > span {
      font-size: 10px;
      position: absolute;
      right: 0px;
      bottom: 13px;
    }
  }
  > div:nth-child(2) {
    grid-area: resource;
    display: grid;
    grid-template-rows: 17px 64px 149px 25px;
    grid-template-areas:
      "."
      "description"
      "img"
      ".";
      justify-items: center;
    > p {
      font-size: 10px;
      grid-area: description;
    }
    > div {
      grid-area: img;
      > img {
        height: 100%;
      }
    }
  }
  > div:nth-child(3) {
    grid-area: acquisition;
    display: grid;
    grid-template-rows: 23px 1fr;
    > div:nth-child(2) {
      align-self: center;
      text-align: left;
      padding-left: 13px;
      font-size: 12px;
    }
  }
  > div:nth-child(4) {
    grid-area: button;
    display: flex;
    justify-content: center;
    > div {
      margin: 0 10px;
      > button {
        display: block;
        width: 29px;
        height: 29px;
        color: map-get($colors, white);
      }
      > span {
        font-size: 10px;
        text-shadow: map-get($shadow, primary-4);
      }
    }
  }
}
</style>
